<template>
    <FormLine :help="help"
        :column="true"
        :viewMode="props.viewMode"
        v-if="props.viewMode === 'list'"
    />

    <template v-if="!data.loading">
        <FormLine :field="fields.instruments" :viewMode="props.viewMode">
            <DictAutoComplete
                :placeholder="t(`dicts.instruments`)"
                :disabled="!props.permissions.canEdit"
                :values="data.instruments"
                dict="instruments"
                class="noerror light with-placeholder"
                @change="onInstrumentsChange"
            />
        </FormLine>

        <FormLine :field="fields.genres" :viewMode="props.viewMode">
            <DictAutoComplete
                :placeholder="t(`dicts.genres`)"
                :values="data.genres"
                :maxValuesCount='3'
                :disabled="!props.permissions.canEdit"
                dict="genres"
                class="noerror light with-placeholder"
                @change="onGenresChange"
            />
        </FormLine>

        <FormLine :field="fields.categories" :viewMode="props.viewMode">
            <DictAutoComplete
                :placeholder="t(`dicts.categories`)"
                :values="data.categories"
                :maxValuesCount='3'
                :disabled="!props.permissions.canEdit"
                dict="categories"
                class="noerror light with-placeholder"
                @change="onCategoriesChange"
            />
        </FormLine>

        <FormLine :field="fields.moods" :viewMode="props.viewMode">
            <DictAutoComplete
                :placeholder="t(`dicts.moods`)"
                :values="data.moods"
                :maxValuesCount='3'
                :disabled="!props.permissions.canEdit"
                dict="moods"
                class="noerror light with-placeholder"
                @change="onMoodsChange"
            />
        </FormLine>

        <FormLine :field="fields.tags" :viewMode="props.viewMode">
            <DictAutoComplete
                :placeholder="t(`dicts.tags`)"
                :values="data.tags"
                :disabled="!props.permissions.canEdit"
                dict="tags"
                class="noerror light with-placeholder"
                @change="onTagsChange"
            />
        </FormLine>
    </template>
</template>

<script setup>
    import { reactive, onMounted } from 'vue';
    import { useI18n } from 'vue-i18n';
    import api from 'ðŸ”—/api';
    
    import DictAutoComplete from 'ðŸ”—/components/ui/DictAutoComplete.vue';
    import FormLine from '@/components/ui/FormLine.vue';

    const emit = defineEmits(['change', 'error', 'metadata', 'save']);
    const { t } = useI18n();
    const dict = api.dict();

    const props = defineProps({
        permissions: { type: Object, default: () => ({}) },
        item: { type: [Object, null], default: null },
        viewMode: { type: String, default: 'list' }
    });


    const help = [
        {
            title: t(`pages.playlists.sections.dicts.subattention1`),
            description: t(`pages.playlists.sections.dicts.subattention1_`)
        }, {
            title: t(`pages.playlists.sections.dicts.subattention2`),
            description: t(`pages.playlists.sections.dicts.subattention2_`)
        }, {
            title: t(`pages.playlists.sections.dicts.subattention1`),
            description: t(`pages.playlists.sections.dicts.subattention2_`)
        }
    ];

    const fields = {
        instruments: {
            name: t('dicts.instruments'),
            width: 200
        },
        genres: {
            name: t('dicts.genres'),
            width: 200
        },
        categories: {
            name: t('dicts.categories'),
            width: 200
        },
        moods: {
            name: t('dicts.moods'),
            width: 200
        },
        tags: {
            name: t('dicts.tags'),
            width: 200
        }
    };

    emit('metadata', {
        fields,
        help,
        direction: 'row'
    });

    const data = reactive({
        instruments: [],
        moods: [],
        categories: [],
        genres: [],
        tags: [],

        loading: true
    });


    const onInstrumentsChange = ({ values }) => {
        data.instruments = values?.length ? values : [];

        onChange();
    }

    const onMoodsChange = ({ values }) => {
        data.moods = values?.length ? values : [];

        onChange();
    }

    const onCategoriesChange = ({ values }) => {
        data.categories = values?.length ? values : [];

        onChange();
    }

    const onGenresChange = ({ values }) => {
        data.genres = values?.length ? values : [];

        onChange();
    }

    const onTagsChange = ({ values }) => {
        data.tags = values?.length ? values : [];

        onChange();
    }

    const onChange = () => {
        const dicts = [
            ...data.instruments.map(item => item.id),
            ...data.moods.map(item => item.id),
            ...data.categories.map(item => item.id),
            ...data.genres.map(item => item.id),
            ...data.tags.map(item => item.id)
        ];

        emit('change', { dicts });
    }

    const load = async () => {
        if (!props.item?.dicts?.length) {
            data.loading = false;
            return;
        }

        data.loading = true;

        const types = {};

        (await dict.getTypes())?.forEach(item => types[item.code] = item.id);

        Object.keys(types).forEach(type => {
            if (data[type]) {
                data[type] = props.item.dicts.filter(item => item.dictId === types[type]).map(item => ({ ...item, name: item.title })) || [];
            }
        });

        data.loading = false;
    }

    onMounted(() => {
        load();
    });
</script>

<style lang="scss" scoped>
</style>
