<template>
    <ItemsEditor
        :t="data.t"
        :code="data.code"
        :canAdd="canAdd"
        :getItem="api.playlists().get"
        :selectItems="selectItems"
        :items="props.items"
        @update="emit('update', $event)"
        @add="emit('add', $event)"
        @remove="emit('remove', $event)"
        @close="emit('close', $event)"
        ref="editor"
    >
        <template #page-header-title>
            <label class="title">{{ t(`pages.playlists.title`) }}</label>
        </template>


        <template #items="{ id, items, change }">
            <Playlists
                :id="id"
                :items="items"
                @change="change"
            />
        </template>

        <template #preview="{ item }">
            <PlaylistPreview :item="item"
                :row="true"
                :showState="true"
            />
        </template>
        
        <template #item="{ toolbarId, item, viewMode, edit, add, update, remove, close, sections, hide, actions }">
            <PlaylistInfo
                :toolbarId="toolbarId"
                :playlist="item"
                :viewMode="viewMode"
                @edit="edit"
                @add="add"
                @update="update"
                @remove="remove"
                @close="close"
                @hide="hide"
                @sections="sections"
                @actions="actions"
            />
        </template>
    </ItemsEditor>
</template>

<script setup>
    import { useI18n } from 'vue-i18n';
    import { reactive, computed, onMounted, ref } from 'vue';

    import ItemsEditor from '@/components/ui/ItemsEditor/ItemsEditor.vue';
    import PlaylistInfo from '../PlaylistInfo/PlaylistInfo.vue';
    import Playlists from './Playlists.vue';
    import PlaylistPreview from 'ðŸ”—/components/ui/PlaylistPreview.vue';
    import api from 'ðŸ”—/api';
    import stores from '@/stores';

    const { t } = useI18n();
    const emit = defineEmits(['update', 'add', 'remove', 'close']);
    const modals = stores.modals();

    const editor = ref(null);


    const props = defineProps({
        items: { type: [Array, null], default: null }
    });

    const data = reactive({
        t: {
            title: 'pages.playlists.title',
            back: 'pages.playlists.back',
            'add-one': 'pages.playlists.add-one',
            'select-several': 'pages.playlists.select-several',
            empty: {
                title: 'pages.playlists.empty.title',
                description: 'pages.playlists.empty.description'
            },
            revert: {
                confirm: {
                    title: 'pages.playlists.revert.confirm.title',
                    description: 'pages.playlists.revert.confirm.description'
                }
            },
            unsaved: {
                confirm: {
                    title: 'pages.playlists.unsaved.confirm.title',
                    description: 'pages.playlists.unsaved.confirm.description'
                }
            }
        },
        code: 'playlist-editor',
    });

    const canAdd = computed(() => {
        return api.playlists().canAdd();
    })

    const create = (count) => {
        editor.value.create(count);
    }

    const selectItems = (items) => {
        modals.open('select-playlists', {
            data: {
                items
            },
            callback: (items) => {
                items?.forEach(item => {
                    editor.value.add(item);
                });
            }
        });
    }

    onMounted(() => {
        if (props.items?.length) {
            data.item = props.items[0];
        }
    });

    defineExpose({
        create
    });
</script>

<style lang="scss" scoped>
    .title {
        font: var(--font-h6);
        font-weight: bold;
        color: var(--dark-500);
    }
</style>
