<template>
    <ItemsSections
        :item="props.item"
        :permissions="props.permissions"
        :viewMode="props.viewMode"
        :sections="data.sections"
        help="pages.playlists.sections.help"

        @change="emit('change', $event)"
        @error="emit('error', $event)"
        @sections="emit('sections', $event)"
        @save="emit('save', $event)"
    />
</template>

<script setup>
    import ItemsSections from '@/components/ui/ItemsEditor/ItemsSections.vue';


    import { reactive, markRaw, onMounted, onBeforeUnmount, ref } from 'vue';
    
    import SectionPlaylistInfo from './SectionPlaylistInfo.vue';
    import SectionMetadata from './SectionMetadata.vue';

    const emit = defineEmits(['change', 'error', 'sections', 'save']);

    const props = defineProps({
        permissions: { type: Object, default: () => ({}) },
        item: { type: [Object, null], default: null },
        viewMode: { type: String, default: 'list' }
    });

    const data = reactive({
        sections: [
            {
                id: 'playlist-info',
                name: '',
                title: 'pages.playlists.sections.playlist-info.name',
                component: markRaw(SectionPlaylistInfo)
            }, {
                id: 'metadata',
                title: 'pages.playlists.sections.dicts.name',
                name: 'pages.playlists.sections.dicts.name',
                component: markRaw(SectionMetadata)
            }
        ]
    });
</script>

<style lang="scss" scoped>
</style>
