<template>
    <div class="playlist-info"
        v-if="!data.loading"
        v-show="!data.updating || props.viewMode === 'table'"
        :class="{
            'view-mode-table': props.viewMode === 'table',
            'updating': data.updating
        }"
        ref="playlistInfoBox"
    >
        <label v-if="props.viewMode === 'list'"
            class="title">{{ t(`pages.playlists.${ props.playlist?.id ? (!canEdit ? 'view' : 'edit') : 'add' }.title`) }}</label>

        <Sections :item="data.item"
            :viewMode="props.viewMode"
            :permissions="{
                canEdit,
                canAddEditorial
            }"
            @change="onSectionChange"
            @error="onSectionError"
            @save="saveChanges"
            @sections="emit('sections', $event)"
        />

        <div :class="props.viewMode === 'table' ? 'actions-box detail column' : ''">
            <Teleport :to="`#${props.toolbarId}`" :disabled="props.viewMode !== 'list'">
                <span class="devider"  v-if="props.viewMode === 'list'" />

                <KebabMenu
                    v-if="data.id && props.viewMode === 'list'"
                    :leftAuto="!locale.rtl"
                    :rightAuto="locale.rtl"
                    :tertiary="true"
                    :invert="true"
                    size="size-s"
                >
                    <ul>
                        <li @click="remove" v-if="canRemove">
                            <Icon icon="trash" />
                            <span>{{ t(`pages.playlists.remove.title`) }}</span>
                        </li>
                        <li @click="close">
                            <Icon icon="close" />
                            <span>{{ t(`pages.playlists.cancel`) }}</span>
                        </li>
                    </ul>
                </KebabMenu>

                <Button :title="t(`pages.playlists.remove.title`)"
                    class="size-m tertiary invert"
                    icon="trash"
                    v-if="canRemove && props.viewMode === 'table' && data.id"
                    @click="remove"
                />

                <StateKebabMenu v-if="data.id"
                    :state="data.item?.state"
                    :states="data.states"
                    :disabled="!canEditState"
                    @change="changeState"
                />

                <Button :name="t(`pages.playlists.${ props.playlist?.id ? 'edit' : 'add' }.save-and-add-more`)"
                    class="size-s secondary"
                    :disabled="saveDisabled"
                    v-if="canEdit && props.viewMode === 'list'"
                    @click="saveChangesAndAddMore"
                />

                <Button :name="t(`pages.playlists.${ props.playlist?.id ? 'edit' : 'add' }.save`)"
                    class="size-s"
                    :disabled="saveDisabled"
                    v-if="canEdit"
                    :loading="data.uploading"
                    :errorsTooltip="errorsTooltip"
                    :class="{
                        tertiary: data.uploading,
                        active: data.uploading,
                        invert: data.uploading,
                        secondary: !data.uploading
                    }"
                    @click="saveChanges"
                />
            </Teleport>
        </div>
    </div>
    <div class="loader" v-if="data.loading || (data.updating && props.viewMode !== 'table')">
        <div>
            <Loader :inside="true" class="tertiary" />
        </div>
    </div>
</template>

<script setup>
    import { reactive, computed, onMounted, ref, watch } from 'vue';

    import Sections from './sections/Sections.vue';

    import { useI18n } from 'vue-i18n';
    import api from 'ðŸ”—/api';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import StateKebabMenu from 'ðŸ”—/components/ui/StateKebabMenu.vue';
    import KebabMenu from 'ðŸ”—/components/ui/KebabMenu.vue';
    import Loader from 'ðŸ”—/components/ui/Loader.vue';
    import { delay, message } from 'ðŸ”—/utils/utils';
    import stores from '@/stores';
    import { code } from 'ðŸ”—/utils/utils';

    const modals = stores.modals();

    const playlists = api.playlists();
    const locale = stores.locale();

    const emit = defineEmits(['update', 'add', 'remove', 'edit', 'sections', 'close', 'hide', 'actions']);
    const { t } = useI18n();

    const playlistInfoBox = ref(null);

    const props = defineProps({
        toolbarId: { type: String, default: '' },
        playlist: { type: [Object, null], default: null },
        viewMode: { type: String, default: 'list' }
    });

    // Permissions
        const canEdit = computed(() => {
            if (data.item?.id) {
                return playlists.canEdit({
                    playlist: data.item
                });
            }

            return playlists.canAdd({
            });
        });

        const canAddEditorial = computed(() => {
            return playlists.canAddEditorial({
                playlist: data.item
            });
        });

        const canRemove = computed(() => {
            return playlists.canRemove({
                playlist: data.item
            });
        });

        const canEditState = computed(() => {
            return playlists.canEditState({
                playlist: data.item
            });
        });

    const data = reactive({
        id: null,
        item: {
            type: 'USER'
        },
        updatedItem: {},
        errors: {},
        states: [],
        loading: true,
        uploading: false
    });

    const getStates = async () => {
        data.states = data.id ? await playlists.getAvailableStates(data.id) || [] : [];
    }

    const changeState = async (state) => {
        modals.confirm({
            title: t(`pages.playlists.state.confirm.title`),
            body: t(`pages.playlists.state.confirm.description`, {
                name: t(`state.default.code.${ state.toLowerCase() }`)
            }),
            onOk: async () => {
                const payload = await playlists.changeState(data.id, state);

                data.item.state = payload?.state;
                getStates();

                emit('update', data.item);

                modals.open('success', {
                    data: {
                        title: t(`pages.playlists.state.done.title`),
                        close: t(`pages.playlists.state.done.close`)
                    },
                    quietClose: true
                });
            }
        }); 
    }

    const onSectionError = (payload) => {
        if (!payload || !Object.keys(payload)?.length) {
            return;
        }

        Object.keys(payload).forEach(field => {
            if (payload[field]?.length) {
                data.errors[field] = payload[field];
            } else {
                delete data.errors[field];
            }
        });
    }

    const onSectionChange = (payload) => {
        if (!payload || !Object.keys(payload)?.length) {
            return;
        }

        Object.keys(payload).forEach(field => data.updatedItem[field] = payload[field]);

        emit('edit', {
            ...data.item,
            ...data.updatedItem
        });
    }

    const hasChanges = computed(() => {
        if (!data.updatedItem) {
            return false;
        }

        return !!Object.keys(data.updatedItem)?.length;
    });

    const saveDisabled = computed(() => {
        return (
            (!data.updatedItem?.title || !data.updatedItem?.title['en-US']?.trim().length) && 
            (!data.item?.title || !data.item?.title['en-US']?.trim().length)
        ) || !!Object.keys(data.errors)?.length || data.uploading || !hasChanges.value;
    });

    const errorsTooltip = computed(() => {
        const errors = [];
        Object.values(data.errors)?.forEach(item => errors.splice(0, 0, ...item));
        return errors.join('<br>');
    });


    const close = () => {
        emit('close', data.item);
    }

    const remove = async () => {
        if (!data.item?.id) {
            return;
        }

        modals.confirm({
            title: t(`pages.playlists.remove.confirm.title`),
            body: t(`pages.playlists.remove.confirm.description`, { title: (data.item?.title || {})['en-US'] }),
            onOk: async () => {
                await playlists.remove(data.id);

                modals.open('success', {
                    data: {
                        title: t(`pages.playlists.remove.done.title`),
                        close: t(`pages.playlists.remove.done.close`)
                    },
                    quietClose: true
                });

                emit('remove', data.item.id);
            }
        }); 
    };

    const saveChanges = async (mode) => {
        let scrollTop = playlistInfoBox.value?.closest('.item-detail')?.scrollTop;

        data.updating = true;

        const playlistInfo = { ...data.updatedItem };

        // Ð£Ð´Ð°Ð»ÑÐµÐ¼ Ð²Ñ€ÐµÐ¼ÐµÐ½Ð½Ñ‹Ð¹ Ð°Ð¹Ð´Ð¸ÑˆÐ½Ð¸Ðº
        delete playlistInfo.localId;

        // Ð£Ð±Ð¸Ñ€Ð°ÐµÐ¼ Ð¿Ð¾Ð»Ñ, ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ðµ Ñ€Ð°ÑÑÑ‡Ð¸Ñ‚Ñ‹Ð²Ð°ÑŽÑ‚ÑÑ Ð°Ð²Ñ‚Ð¾Ð¼Ð°Ñ‚Ð¸Ñ‡ÐµÑÐºÐ¸

        // Ð’Ñ‹Ñ‚Ð°ÑÐºÐ¸Ð²Ð°ÐµÐ¼ ÐºÐ°Ñ€Ñ‚Ð¸Ð½ÐºÑƒ
        const cover = playlistInfo.cover;
        if (cover) {
            delete playlistInfo.cover;
        }

        // Ð¡ÐµÐ¹Ð²Ð¸Ð¼ Ð¾ÑÐ½Ð¾Ð²Ð½Ñ‹Ðµ Ð´Ð°Ð½Ð½Ñ‹Ðµ
        let payload;

        if (data.id) {
            payload = await playlists.update(data.id, playlistInfo);
        } else {
            payload = await playlists.add(playlistInfo);

            emit('actions', {
                item: data.item,
                actions: []
            });
        }

        // ÐžÐ±Ð½Ð¾Ð²Ð»ÑÐµÐ¼ ÐºÐ°Ñ€Ñ‚Ð¸Ð½ÐºÑƒ
        if (payload?.id && cover) {
            payload = await playlists.updateCover(payload.id, cover);
        }

        await delay(200);
        data.updating = false;

        if (payload?.id) {
            message.info(t(`pages.playlists.${ data.id ? 'edit' : 'add' }.done.title`));
        }

        if (data.id) {
            emit('update', payload, mode);
        } else {
            emit('add', {
                ...payload,
                localId: data.item?.localId
            }, mode);
        }

        data.item = payload;
        data.updatedItem = {};
        data.code = payload.code;
        data.id = payload.id;

        if (props.viewMode === 'list' && scrollTop) {
            setTimeout(() => {
                if (playlistInfoBox?.value) {
                    playlistInfoBox.value.closest('.item-detail').scrollTop = scrollTop;
                }
            });
        }
    }

    const saveChangesAndAddMore = async () => {
        await saveChanges('add-one');
    }

    const getItem = async () => {
        data.loading = true;

        const payload = await playlists.get(props.playlist.id);

        data.id = payload?.id;
        data.item = payload || {};

        await getStates();

        await delay(200);

        data.loading = false;
    }

    const createItem = () => {
        data.item = {
            type: 'USER'
        }

        if (props.playlist?.localId) {
            data.item = { ...data.item, ...props.playlist };
            // data.item.localId = props.playlist.localId;
        }

        data.updatedItem = { ...data.item };
        data.states = [];
        data.errors = {};
        data.id = null;

        emit('edit', {
            ...data.item,
            ...data.updatedItem
        });
    }

    watch(
        () => saveDisabled.value,
        (value) => {
            if (value) {
                emit('actions', {
                    item: data.item,
                    actions: []
                });
            } else {
                emit('actions', {
                    item: data.item,
                    actions: [
                        {
                            type: props.track?.id ? 'save' : 'create',
                            action: saveChanges
                        }
                    ]
                });
            }
        }
    );

    onMounted(() => {
        if (props.playlist?.id) {
            getItem();
        } else {
            createItem();
            data.loading = false;
        }
    });
</script>

<style lang="scss" scoped>
    @include studio.actions-box;

    div.loader {
        @include inside-loader;
        min-height: 132px;
    }

    .errors {
        width: 18px;
        height: 18px;
        fill: var(--white-500);
        opacity: 0;
        pointer-events: none;
        &.showed {
            opacity: 1;
            pointer-events: auto;
        }
    }

    .playlist-info {
        display: flex;
        flex-direction: column;
        gap: 24px;
        .title {
            font: var(--font-h3);
            font-weight: bold;
            color: var(--dark-500);
        }

        &.view-mode-table {
            flex-direction: row;
            gap: 0;

            .actions-box {
                width: 140px;
                box-shadow: -8px 0px 8px -12px var(--dark-500);
                z-index: 1;
                position: sticky;
                right: 0;
                flex-direction: column-reverse;
                justify-content: start;
                padding: 18px 8px;
            }

            &.updating {
                pointer-events: none;
                position: relative;
                &:after {
                    content: '';
                    opacity: .6;
                    @include skeleton;
                }

                .actions-box {
                    pointer-events: none;
                }
            }
        }
    }
    .direction-rtl .playlist-info {
        &.view-mode-table {
            .actions-box {
                right: auto;
                left: 0;
                box-shadow: 8px 0px 8px -12px var(--dark-500);
            }
        }
    }
</style>
