<template>
    <PageHeader :title="t(`pages.playlists.title`)"
        :detail="!!data.selectedPlaylists?.length"
        id="item-head-buttons"
        v-show="!data.playlistEditorMode && !data.trackEditorMode"
    />

    <div class="box" v-show="!data.playlistEditorMode && !data.trackEditorMode">
        <div class="item-detail">
            <Playlists
                @select="onSelectPlaylists"
                @add="addPlaylists"
                @edit="editPlaylists"
                @editTracks="editPlaylistTracks"
                ref="playlists"
            />
        </div>
    </div>

    <!-- Ð ÐµÐ´Ð°ÐºÑ‚Ð¸Ñ€Ð¾Ð²Ð°Ð½Ð¸Ðµ Ð¿Ð»ÐµÐ¹Ð»Ð¸ÑÑ‚Ð¾Ð² -->
    <PlaylistEditor v-if="data.playlistEditorMode"
        :items="data.editorPlaylists"
        ref="playlistEditor"
        @remove="onRemovePlaylist"
        @update="onUpdatePlaylist"
        @add="onAddPlaylist"
        @close="closePlaylistEditorMode"
    />

    <!-- Ð ÐµÐ´Ð°ÐºÑ‚Ð¸Ñ€Ð¾Ð²Ð°Ð½Ð¸Ðµ Ñ‚Ñ€ÐµÐºÐ¾Ð² -->
    <TrackEditor v-if="data.trackEditorMode"
        :playlist="data.editorPlaylists?.length ? data.editorPlaylists[0] : null"
        :items="data.editorTracks"
        ref="trackEditor"
        @remove=""
        @update=""
        @add=""
        @close="closeTrackEditorMode"
    />
</template>

<script setup>
    import { reactive, computed, ref } from 'vue';
    import PageHeader from '@/components/blocks/PageHeader/PageHeader.vue';

    import Playlists from '@/components/blocks/Playlists.vue';
    import PlaylistEditor from '@/components/blocks/PlaylistEditor/PlaylistEditor.vue';
    import TrackEditor from '@/components/blocks/TrackEditor/TrackEditor.vue';
    import api from 'ðŸ”—/api';

    import { useI18n } from 'vue-i18n';

    const { t } = useI18n();
    const playlists = ref(null)
    const playlistEditor = ref(null);
    const trackEditor = ref(null);

    const data = reactive({
        playlistEditorMode: false,
        editorPlaylists: null,
        trackEditorMode: false,
        editorTracks: null,
        selectedPlaylists: null
    });

    // Playlists
    const onSelectPlaylists = (playlists) => {
        data.selectedPlaylists = playlists?.length ? playlists : null;
    }

    const addPlaylists = (count) => {
        data.editorPlaylists = [];
        data.editorTracks = [];
        data.playlistEditorMode = true;

        // 
        setTimeout(() => {
            playlistEditor.value?.create(count || 1);
        });
    }

    const editPlaylists = (playlists) => {
        data.editorPlaylists = playlists;
        data.playlistEditorMode = true;
    }

    const editPlaylistTracks = async (playlist) => {
        const payload = await api.playlists().tracks.list(playlist.id);

        data.editorTracks = payload?.items || [];

        data.editorPlaylists = [...(data.selectedPlaylists || [])];
        data.trackEditorMode = true;
    }

    const closePlaylistEditorMode = () => {
        data.playlistEditorMode = false;
    }
        // Events
        const onAddPlaylist = (playlist) => {
            if (playlist?.id) {
                playlists.value.addItem(playlist);
            }
        }

        const onRemovePlaylist = (id) => {
            if (id) {
                playlists.value.removeItems([{ id }], true)
            }
        }

        const onUpdatePlaylist = (playlist) => {
            if (playlist?.id) {
                playlists.value.updateItem(playlist);
            }
        }

    // Tracks
        const closeTrackEditorMode = () => {
            data.trackEditorMode = false;
        }
</script>

<style lang="scss" scoped>
    @include studio.default-box;
</style>
